@import url('https://fonts.googleapis.com/css?family=Open+Sans:400,700');

body {
  background: -webkit-gradient(linear, left bottom, left top, from(#ebc0fd), to(#d9ded8));
  background: -webkit-linear-gradient(bottom, #ebc0fd 0%, #d9ded8 100%);
  background: -o-linear-gradient(bottom, #ebc0fd 0%, #d9ded8 100%);
  background: linear-gradient(to top, #ebc0fd 0%, #d9ded8 100%);
  width: 100%;
  height: 100vh;
  font-family: 'Open Sans', sans-serif;
  color: #444;
}

.autocomplete {
  position: absolute;
  top: 45%;
  left: 50%;
  -webkit-transform: translate(-50%, -55%);
      -ms-transform: translate(-50%, -55%);
          transform: translate(-50%, -55%);
}

.autocomplete__input {
  padding: 20px;
  -webkit-box-shadow: 0 7px 30px rgba(0,0,0,0.2);
          box-shadow: 0 7px 30px rgba(0,0,0,0.2);
  outline: none;
  border: none;
  min-width: 400px;
  border-radius: 3px;
  color: #333;
  letter-spacing: 0.3px;
}

.autocomplete__suggestions {
  width: 100%;
  position: absolute;
  left: 0;
  top: 70px;
  background: #fff;
  -webkit-box-shadow: 0 7px 30px rgba(0,0,0,0.2);
          box-shadow: 0 7px 30px rgba(0,0,0,0.2);
  border-radius: 4px;
}

.autocomplete__suggestions-inner {
  padding: 20px;
}

.autocomplete__suggestions-item {
  display: block;
  margin-bottom: 5px;
  -webkit-transition: color 0.15s ease-in-out;
  -o-transition: color 0.15s ease-in-out;
  transition: color 0.15s ease-in-out;
  cursor: pointer;
  outline: none;
}

.autocomplete__suggestions-item:last-child {
  margin-bottom: 0;
}

.autocomplete__suggestions-item:hover {
  color: purple;
}

.autocomplete__suggestions-link {
  border-bottom: 1px solid transparent;
  padding-bottom: 1px;
}

.autocomplete__suggestions-item:focus .autocomplete__suggestions-link {
  color: purple;
  border-color: purple;
}

.arrow-up {
  position: absolute;
  top: -9px;
  left: 50%;
  -webkit-transform: translate(-50%, 0);
      -ms-transform: translate(-50%, 0);
          transform: translate(-50%, 0);
  width: 0; 
  height: 0; 
  border-left: 10px solid transparent;
  border-right: 10px solid transparent;
  border-bottom: 10px solid #fff;
}

::-webkit-input-placeholder { /* Chrome/Opera/Safari */
  color: #aaa;
  font-weight: bold;
}
::-moz-placeholder { /* Firefox 19+ */
  color: #aaa;
  font-weight: bold;
}
:-ms-input-placeholder { /* IE 10+ */
  color: #aaa;
  font-weight: bold;
}
:-moz-placeholder { /* Firefox 18- */
  color: #aaa;
  font-weight: bold;
}


